import React from 'react';
import Header from './header';
import Inventory from './inventory';
import Order from './order';
import sampleFishes from '../sample-fishes';
import Fish from './fish';
import base from '../base';

class App extends React.Component{
  constructor() {
    super();
    this.addFish = this.addFish.bind(this);
    this.removeFish = this.removeFish.bind(this);
    this.loadSamples = this.loadSamples.bind(this); //this allows loadSamples to be bound to this (this can now be used inside loadSamples)
    this.addToOrder = this.addToOrder.bind(this);
    this.removeFromOrder = this.removeFromOrder.bind(this);
    this.updateFish = this.updateFish.bind(this);
    // this method is similar to getInitialState({})
    this.state={
      fishes: {},
      order: {}
    }
  }

  componentWillMount(){
    this.ref = base.syncState(`${this.props.params.storeId}/fishes`,
      {
        context: this,
        state: 'fishes'
      });

    const localStorageRef = localStorage.getItem(`order-${this.props.params.storeId}`);

    if (localStorageRef) {
      this.setState({
        order: JSON.parse(localStorageRef)
      })
    }
  }

  componentWillUnmount() {
    base.removeBinding(this.ref);
  }

  componentWillUpdate(nextProps, nextState) {
  //  console.log("components will updlate changes (next Props, nextState)");
    console.log(nextProps, nextState);
    localStorage.setItem(`order-${this.props.params.storeId}`,
      JSON.stringify(nextState.order))
  }

  addFish(fish) {
    //update our states
    //set state
    const fishes = {...this.state.fishes};

    //timestamp to support format: fish-timestamp1, fish-timestamp2, fish-timestamp3, &c.
    const timestamp = Date.now();

    fishes[`fish-${timestamp}`] = fish;

    this.setState({ fishes }); //same thing as fishes: fishes
  }

  updateFish(key, updatedFish){
    const fishes = {...this.state.fishes};
    fishes[key] = updatedFish;
    this.setState({ fishes });
  }

  removeFish(key){
    const fishes = {...this.state.fishes}; //takes a copy of fishes object
    //delete fishes[key] #=> This should have been suffice, but we have firebase now and mere delete method won't work.
    fishes[key] = null; //removes it/ makes it null
    this.setState({fishes});
  }

  loadSamples() {
    this.setState({
      fishes: sampleFishes
    })
  }

  addToOrder(key){
    const order = {...this.state.order};
    order[key] = order[key] + 1 || 1;
    this.setState({
      order: order
    });
  }

  removeFromOrder(key){
    const order= {...this.state.order};
    delete order[key];
    this.setState({ order })
  }

  render(){
    return(
      <div className="catch-of-the-day">
        <div className="menu">
          <Header tagline={"fresh seafood market"} /> {/*Component, not HTML header element */}
            <ul className="list-of-fishes">
              {
                Object
                  .keys(this.state.fishes)
                  .map(key => <Fish key={key} index={key} details={this.state.fishes[key]} addToOrder={this.addToOrder}/>)
              }
            </ul>
        </div>
        <Order params={this.props.params} fishes={this.state.fishes} order={this.state.order} removeFromOrder={this.removeFromOrder}/>
        <Inventory updateFish={this.updateFish} addFish={this.addFish} loadSamples={this.loadSamples} fishes={this.state.fishes} removeFish={this.removeFish} storeId = {this.props.params.storeId}/>
      </div>
    )
  }
};

App.propTypes = {
  params: React.PropTypes.object.isRequired
}
export default App;



