import React from 'react';
import {getFunName} from '../helpers';

class StorePicker extends React.Component{
  // constructor() {
  //   super();
  //   this.goToSTore = this.goToStore.bind(this);
  // }
  goToStore(event) {
    event.preventDefault();
    console.log("You changed da URL");
    console.log(event);
    console.log(this);
    console.log(this.storeInput);
    const storeId = this.storeInput.value;
    console.log(storeId);
    this.context.router.transitionTo(`/store/${storeId}`)
  }

  render() {
    return (
      <form className="store-selector" onSubmit={this.goToStore.bind(this)}> {/*go to this (StorePicker, because it is accessible from render, then go to goToStore, and bind goToStore to this) */}
        <h2>Please enter a store: </h2>
        {/* User submits information */}
        <input type="text" required placeholder="Enter Store Name" defaultValue={getFunName()} ref={(input) => {this.storeInput = input}}/>
        <button type="submit">Visit store</button>
        <button type="reset">Reset</button>
      </form>
    )
  }
}

StorePicker.contextTypes = { //surface the router to parent (StorePicker) so router is available to children (including goToStore)
  router: React.PropTypes.object
}

export default StorePicker



