import React from 'react';

class AddFishForm extends React.Component{
  createFish(event) {
    console.log("Yum yum!");
    const fish = {
      name: this.name.value,
      price: this.price.value,
      status: this.status.value,
      desc: this.desc.value,
      image: this.image.value
    }
    console.log(fish);

    this.props.addFish(fish);

    this.fishForm.reset();

  }
  render(){
    return (
      <form ref={(input) => this.fishForm = input} className="fish-edit" onSubmit={(e) => this.createFish(e)}>
        <input type="text" ref={(input) => this.name = input} placeholder="Add fish name" />

        <input type="text" ref={(input) => this.price = input} placeholder="Add fish price" />

        <select ref={(input) => this.status = input}>
          <option value="available">Fresh!</option>
          <option value="unavailable">Sold out!</option>
          <option value="unavailable">In the ocean :3</option>
        </select>

        <textarea type="text" ref={(input) => this.desc = input} placeholder="Add fish desc" />

        <input type="text" ref={(input) => this.image = input} placeholder="Add fish image" />

        <button type="submit">+ fish</button>
      </form>
    )
  }
};

AddFishForm.propTypes = {
  addFish: React.PropTypes.func.isRequired
}
export default AddFishForm;



