import React from 'react';
import AddFishForm from './addfishform';
import base from '../base';

class Inventory extends React.Component{
  constructor(){
    super();
    this.renderInventory = this.renderInventory.bind(this);
    this.renderLogin = this.renderLogin.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.authenticate = this.authenticate.bind(this);
    this.authHandler = this.authHandler.bind(this);
    this.logOut = this.logOut.bind(this);
    this.state = {
      uid: null,
      owner: null
    }
  }

  componentDidMount(){
    base.onAuth((user) => {
      if(user) {
        this.authHandler(null, {user});
      }
    });
  }

  handleChange(e, key){
    const fish = this.props.fishes[key];
    console.log(fish)
    const updatedFish = {
      ...fish,
      [e.target.name]: e.target.value
    }
    this.props.updateFish(key, updatedFish);
    console.log(updatedFish);
  }

  authenticate(provider) {
    console.log(`Trying to log in with ${provider}`);
    base.authWithOAuthPopup(provider, this.authHandler);
  }

  logOut() {
    base.unauth();
    this.setState({
      uid: null
    })
  }

  authHandler(err, authData) {
    console.log(authData);
    if (err) {
      console.log(err);
      return;
    }
    const storeRef = base.database().ref(this.props.storeId);
    storeRef.once('value', (snapshot) => {
      const data = snapshot.val() || {};

      if (!data.owner) {
        storeRef.set({
          owner: authData.user.uid
        })
      }

      this.setState({
        uid: authData.user.uid,
        owner: data.owner || authData.user.uid
      })
    })
  }

  renderLogin() {
    return (
      <nav className="login">
        <h2>Inventory</h2>
        <p>Please Sign In to manage your Store's directory:</p>
        <button className="facebook" onClick={() => this.authenticate('facebook')}>Log in with facebook</button>
      </nav>
    )
  }

  renderInventory(key){
    const fish = this.props.fishes[key] //key is well, key, i.e.: 'fish1'
    return(
      <div className="fish-edit" key={key}>
        <input type="text" name="name" placeholder = "Enter fish name" value={fish.name} onChange={(e) => this.handleChange(e, key)}/>
        <input type="text" name="price" placeholder = "Enter price" value={fish.price} onChange={(e) => this.handleChange(e, key)}/>

        <select name="status" placeholder="Enter status" onChange={(e) => this.handleChange(e, key)}>
          <option value="available">Fresh!</option>
          <option value="unavailable">Sold out!</option>
        </select>

        <textarea type="text" name="desc" placeholder = "Enter desc" value={fish.desc} onChange={(e) => this.handleChange(e, key)}/>
        <input type="text" name="image" placeholder= "Enter image" value={fish.image} onChange={(e) => this.handleChange(e, key)}/>
        <button onClick={() => this.props.removeFish(key)}>Remove Fish from Inventory</button>
      </div>
    )
  }

  render(){
    const logout = <button onClick={this.logOut}>Log out</button>
    //checks if they are NOT logged in at all
    if (!this.state.uid) {
      return <div>{this.renderLogin()}</div>
    }

    if (this.state.uid !== this.state.owner) {
      return (
        <div>
          <p>Sorry, you are not the owner of this store!</p>
          {logout}
        </div>
      )
    }
    return (
      <div>
        <h2> Inventory </h2>
        {logout}
        {Object.keys(this.props.fishes).map(this.renderInventory)}
        <AddFishForm addFish={this.props.addFish} />
        <button onClick={this.props.loadSamples}> Load Sample Fishes </button>
      </div>
    )
  }
};

Inventory.propTypes = {
  fishes: React.PropTypes.object.isRequired,
  updateFish: React.PropTypes.func.isRequired,
  removeFish: React.PropTypes.func.isRequired,
  addFish: React.PropTypes.func.isRequired,
  loadSamples: React.PropTypes.func.isRequired,
  storeId: React.PropTypes.string.isRequired

}

export default Inventory;



